<template>
  <div class="add-card-wrap">
    <!-- <p class="msg-tip">*</p> -->
    <div class="msg-box">
      <div class="item-box">
        <p></p>
        <span class="r">{{personalData.cardName | filterName}}</span>
      </div>
      <div class="item-box">
        <p></p>
        <span class="r">{{personalData.idCard | filterCard}}</span>
      </div>
    </div>
    <div class="msg-box">
      <div class="item-box">
        <p>*</p>
        <span class="r" @click="showArea = true">{{formData.selectCity.map(row => row.name).join(' ') || ''}}</span>
      </div>
      <div class="item-box">
        <p>*</p>
        <input type="text" placeholder="" maxlength="28" v-model="formData.cardNo" @blur="inputBlur">
      </div>
      <div class="item-box">
        <p>*</p>
        <input type="text" placeholder="" v-model="formData.bankName" maxlength="20">
      </div>
      <div class="item-box">
        <p>*</p>
        <input type="text" placeholder="" v-model="formData.branchBankName" maxlength="50">
      </div>
      <div class="item-box">
        <p>*</p>
        <input type="text" placeholder="" maxlength="11" v-model="formData.phone">
      </div>
    </div>
    <div class="submit-box">
      <div class="submit-btn" @click="cardInfoSubmit"></div>
    </div>
     <!--  -->
    <van-popup
      v-model="showArea"
      position="bottom"
    >
      <van-area :area-list="areaList" @confirm="getAreaData" @cancel="showArea = false"/>
    </van-popup>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { Icon, Button, Popup, Area, ActionSheet, Toast } from 'vant'
import areaList from '../../../../../assets/js/area'
import BIN from 'bankcardinfo'
export default {
  components: {
    [Icon.name]: Icon,
    [Button.name]: Button,
    [Popup.name]: Popup,
    [Area.name]: Area,
    [ActionSheet.name]: ActionSheet,
    [Toast.name]: Toast
  },
  data () {
    return {
      userInfo: {},
      areaList: areaList,
      showArea: false,
      addressCode: [], // code
      title: '',
      cityName: '',
      branchBankInfo: '',
      addressData: '',
      flag: false,          // 
      formData: {
        cardNo: '',
        bankName: '',
        province: '',
        provinceCode: '',
        city: '',
        cityCode: '',
        zone: '',
        zoneCode: '',
        branchBankName: '',
        registerName: '',
        cardType: '',
        phone: '',
        selectCity: []
      }
    }
  },
  mounted () {
    this.userInfo = JSON.parse(localStorage.getItem('LZWLUSERINFO'))
  },
  computed: {
    ...mapState(['personalData'])
  },
  methods: {
    /**
     * 
    */
    getAreaData (val) {
      this.showArea = false
      this.formData.selectCity = val
    },
    /**
     * 
     */
    saveAddress (flag) {
      if (flag) {
        this.cityName = this.getName(this.addressCode).split(' ')
        this.formData.province = this.cityName[0]
        this.formData.provinceCode = this.addressCode[0]
        this.formData.city = this.cityName[1]
        this.formData.cityCode = this.addressCode[1]
        this.formData.zone = this.cityName[2]
        this.formData.zoneCode = this.addressCode[2]
      }
    },
    /**
     * code
     */
    getName (value) {
    },
    /**
    * 
    */
    inputBlur () {
      this.formData.bankName = ''
      this.identifyCard()
    },
    /**
    * 
    */
    cardInfoSubmit () {
      this.identifyCard()
      if (!this.flag) {
        return false
      }
      if (this.formData.selectCity.length <= 0) {
        Toast('')
        return false
      } if (this.formData.cardNo === '') {
        Toast('')
        return false
      } if (this.formData.bankName === '') {
        Toast('')
        return false
      } if (this.formData.branchBankName === '') {
        Toast('')
        return false
      }
      if (this.formData.phone === '') {
        Toast('')
        return false
      }
      if (!this.formData.phone.match(/1\d{10}/)) {
        Toast('')
        return false
      }
      this.$http.post(`${this.api.saveCard}`, this.formData).then((res) => {
        this.$toast(res.data.msg)
        if (res.data.status !== this.api.ERR_OK || !res) {
          return false
        }
        this.$router.back()
      })
    },
    /**
      * 
    */
    identifyCard () {
      if (this.formData.cardNo === '') {
        this.$toast('')
        return false
      }
      let reg = /^\d+$/
      if (!reg.test(this.formData.cardNo)) {
        this.$toast('')
        return false
      }
      BIN.getBankBin(this.formData.cardNo).then(data => {
        this.flag = true
        if (data.cardType === 'CC') {
          this.$toast('')
          return false
        }
        this.formData.bankName = data.bankName
        this.formData.cardType = data.cardType === 'DC' ? 1 : 2
      }).catch(() => {
        this.$toast('')
        this.flag = false
      })
    }
  }
}
</script>

<style scoped lang="less">
.add-card-wrap{
  text-align: left;
  height: calc(100vh);
  overflow: auto;
  background: #1c1e3a;

  .msg-tip{
    height: 1rem;
    line-height: 1rem;
    text-align: center;
    color: #FF0000;
    font-size: .28rem;
  }

  .msg-box{
    margin-bottom: .2rem;
    width: 7rem;
    margin: 0.34rem auto;
    background: rgba(42, 49, 84, 0.6);
    padding: 0 0.4rem;
    box-sizing: border-box;

    .item-box{
      display: flex;
      height: 1rem;
      line-height: 1rem;
      border-bottom: 1px solid #2A3154;
      &:last-child{
        border-bottom: none;
      }
      p{
        display: inline-block;
        width: 1.4rem;
        font-size: 24px;
        color: #DFE2F4;
      }
      .r{
        color: #DFE2F4;
      }
      input, .r{
        flex: 1;
        background: none;
        text-align: right;
        font-size: 24px;
        color: #8384A0;
      }
      input{
        &::-webkit-input-placeholder {
          color:#8384A0;
        }
      }
    }
  }

  .submit-box{
    padding: .4rem .2rem;
    .submit-btn{
      width:622px;
      height:81px;
      line-height: 81px;
      background:rgba(105,129,251,1);
      border-radius:10px;
      font-size: 0.28rem;
      cursor: pointer;
      border: none;
      margin: 0 auto;
      cursor: pointer;
      margin-top: 59px;
      text-align: center;
      color: #fff;
    }
  }
}
</style>
